
.image {
  background-color: #000;
}
.input {
  max-width: 400px;
  margin: 0 auto;
}
.row {
  padding-top: 20px;
}
.container {
  padding-bottom: 150px;
}
.ivu-table-cell {
  padding: 18px;
}
.ivu-table-cell img {
  max-width: 100%;
}
.page {
  z-index: 99999;
  width: 100%;
  position: fixed;
  text-align: center;
  padding-bottom: 20px;
  background-color: #fff;
  /*border-top:1px solid #e9eaec;*/
  box-shadow: 0 0 10px #dddee1;
  left: 0;
  bottom: 0;
}
.page .ivu-select-dropdown {
  top: auto !important;
  bottom: 50px !important;
}



